// Normally mainpage styles
.checkbox
    position: relative
    display: inline-block
    height: 40px 
    line-height: 1
    padding: 12px 20px 0
    +bold
    color: #32394d
    user-select: none
    cursor: pointer
    padding-left: 28px

    input
        position: absolute
        top: 1px
        left: 0
        width: 20px
        height: 20px
        opacity: 0
        visibility: hidden
        z-index: 2
        cursor: pointer
        &:checked + .checkbox__text
            &:after
                opacity: 1
    &__text
        display: block
        &:before,
        &:after
            position: absolute
            content: ""
        &:before
            top: 12px
            left: 0
            width: 16px
            height: 16px
            border: 1px solid #e7ebee
            transition: border-color .2s ease-in-out

        &:after
            content: "" 
            top: 17px
            left: 3px
            width: 6px
            height: 13px
            transition: opacity .2s ease-in-out
            opacity: 0
            +s('chevron-down-red')

.tag
    position: relative
    display: inline-block
    +bold
    user-select: none
    cursor: pointer
    vertical-align: top
   
    input
        position: absolute
        top: 1px
        left: 0
        width: 20px
        height: 20px
        opacity: 0
        visibility: hidden
        z-index: 2
        cursor: pointer
        &:checked + .tag__text
            padding: 4px 20px 3px 10px
            color: #cc2b23
            border-color: #e7ebee
            &:after
                opacity: 1
    &__text
        display: inline-block
        padding: 4px 0 3px
        border: 1px solid transparent
        border-radius: 15px
        text-transform: uppercase
        color: #7a7c81
        &:after
            content: ""
            position: absolute
            top: 10px
            right: 8px
            transition: opacity .2s ease-in-out
            opacity: 0
            +s('close-sm')

.content-section
    &:not(:last-child)
        margin-bottom: 30px

.pay-table
    width: 100% 
    table-layout: fixed 
    margin-bottom: 10px
    text-align: center
    td
        &.empty-cell
            empty-cells: show
    &__title
        padding: 20px 10px
        background-color: #f8f8f8
        text-transform: uppercase
        color: #444
        +rm
        font-size: 20px
        line-height: 1.2
    &__subtitle
        padding: 10px 
        background-color: #ebebeb
        text-transform: uppercase
        color: #666
        font-size: 15px
        +bold
        line-height: 1.4
    &__data
        padding: 10px 
        color: #666
        font-size: 15px
        +bold
        line-height: 1.4
        
.news-wrap
    margin: 0 -15px

.news
    font-size: 0
.news-tile
    display: inline-block
    vertical-align: top
    width: 50% 
    margin-bottom: 20px
    padding: 0 15px 
    &__inner
        overflow: hidden
        border-radius: 2px
        box-shadow: 0 0 4px rgba(0, 0, 0, 0.12)
        text-decoration: none
        &:hover
            .news-tile__title
                border-color: currentColor
    &__body
        padding: 0 15px 20px
    &__head
        margin-bottom: 10px
        
    &__title
        display: inline-block
        color: #717171 
        font-size: 21px
        border-bottom: 2px solid transparent
        +bold
        line-height: 1.2
        transition: border-color 0.2s
    &__text
        font-size: 14px
        color: #acaaaa
        line-height: 1.3
    &__img-wrap
        margin-bottom: 10px
        img
            max-width: 100% 
            width: auto
            height: 100% 
            object-fit: cover 
            
.popup-bl
    display: none
    position: absolute
    top: 100% 
    right: 0
    width: 100% 
    margin-top: 40px  
    background-color: white
    border-radius: 4px
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.33)
    overflow: hidden    
    &.w775
        width: 875px 
    &__inner
    &__head
        background-color: #cc2b23
        display: flex
        height: 48px 
        padding: 0 25px
        flex-direction: row
        flex-wrap: nowrap
        justify-content: space-between
        align-items: center
    &__body
        padding: 15px 17px 25px
    &__title
        float: left
        color: #fefefe
        +rm
        font-size: 15px
        text-transform: uppercase
    &__user
        float: right
        
.user-auth
    &__img, &__text
        display: inline-block
        vertical-align: middle
    &__img
        margin-right: 10px 
    &__text
        font-size: 11px
        color: #fefefe
        +rm
    &__link
        position: relative
        display: inline-block
        padding-right: 15px 
        font-size: 9px
        font-family: Arial, sans-serif 
        color: #FFF 
        +bold
        &:after
            content: "" 
            position: absolute
            top: 50%
            right: 0  
            margin-top: -2px
            width: 0
            height: 0
            border: 4px solid transparent
            border-top: 4px solid #f3db0e 

.order
    +clr
    &__table
        width: 100% 
        margin-bottom: 15px
    tr
        &:first-child
            td
                padding: 20px 0 10px 
    td
        vertical-align: middle
        padding: 10px
        border-bottom: 1px solid #f4f4f4
        &.align-left
            text-align: left
    &__thead
        padding: 10px 
        background-color: #f4f4f4
        color: #333
        font-size: 10px
        &.align-left
            text-align: left
        &:first-child
            border-radius: 4px 0 0 4px
        &:last-child
            border-radius: 0 4px 4px 0 
    &__img-wrap 
        margin: 0 auto 
        height: 47px 
        width: 47px 
        border: 1px solid #ddd
        img
            width: auto 
            max-width: 100% 
            height: 100% 
            
    &__product  
        text-align: left
        &-title, &-code
            text-transform: uppercase
            +bold
        &-title
            color: #cc2b23
            font-size: 13px
            text-decoration: none
            &:hover
                text-decoration: underline    
        &-code
            color: #333
            font-size: 10px
    &__values
        color: #333
        font-size: 13px
        +bold
    &__all-sum
        float: right
        display: inline-block
        padding: 13px 20px 
        background-color: #f4f4f4
        white-space: nowrap
        &-text, &-value
            display: inline-block
            font-size: 19px
            +bold
            color: #333
            text-transform: uppercase
        &-text
            margin-right: 10px

    &__submit
        float: right
        height: 25px 
        line-height: 25px
        padding: 0 17px
        background-color: #376c2c
        border-radius: 4px
        font-size: 10px
        +bold
        color: #FFF 
    &__row
        +clr
        &:not(:last-child)
            margin-bottom: 15px